<template>
  <div class="repair-bg" id="work-history">
    <div class="bq-parent" v-if="shutype == 'heng'">
      <blockquote class="blockquote">
        <p>用户基本信息</p>
      </blockquote>
    </div>
    <!-- 报建流程信息 -->
    <div class="panel panel-default auto repair-info-content">
      <div class="panel-body">
        <p>客户姓名： {{service.f_user_name}}</p>
        <p>客户电话： {{service.f_phone}}</p>
        <p>客户地址： {{service.f_address}}</p>
        <p>客户类型： {{service.f_user_type}}</p>
        <p>客户片区： {{service.f_residential_area}}</p>
        <p>客户小区： {{service.f_area}}</p>
        <!--<p>申请户数： {{service.f_apply_count}}</p>-->
        <!--<p>房屋类型： {{service.f_house_type}}</p>-->
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    title: '用户基本信息',
    data () {
      return {
        // service: null
        f_userinfo_code : ''
      }
    },
    props: {
      service: { // 传入一条报修单信息
        type: Object
      },
      shutype:'heng'
    },
    ready () {
    },
    methods: {
      selfSearch () {
        if(!this.rowOne) {
          return
        }
      }
    },
    watch: {
      'rowOne' () {
        this.selfSearch()
      }
    },
    components: {

    }
  }
</script>



