<template>
  <div class="repair-bg" id="work-history">
    <div class="bq-parent" v-if="shutype == 'heng'">
      <blockquote class="blockquote">
        <p>报建流程信息</p>
      </blockquote>
    </div>
    <!-- 报建流程信息 -->
    <div class="panel panel-default auto repair-info-content">
      <div class="panel-body">
        <p>报建编号： {{service.f_apply_num}}</p>
        <p>报建类型： {{service.f_apply_type}}</p>
        <p>流程状态： {{service.f_order_type}}</p>
        <p>报建日期： {{service.f_apply_date}}</p>
        <p>报建备注： {{service.f_apply_remarks}}</p>
        <failure-show :value='service.failure'>
        </failure-show>
      </div>
    </div>
  </div>
  </template>
  <script>
  export default {
    title: '报建流程信息',
    data () {
      return {
        // service: null
        f_userinfo_code : ''
      }
    },
    props: {
      service: { // 传入一条报修单信息
       //"f_apply_num":"123456",
        type: Object
      },
      shutype:'heng'
    },
    ready () {
    },
    methods: {
      selfSearch () {
        if(!this.rowOne) {
          return
        }
      }
    },
    watch: {
      'rowOne' () {
        this.selfSearch()
      }
    },
    components: {

    }
  }
  </script>



